@import url('https://fonts.googleapis.com/css2?family=Maitree&display=swap');

@font-face {
    font-family: 'Maitree';
    src: url("../fonts/RIDIBatang_min.otf") format("opentype");
    font-style: normal;
    unicode-range: U+AC00-D7A3;
}

@font-face {
    font-family: 'Eulyoo1945';
    src: url("../fonts/Eulyoo1945-Regular.ttf") format("truetype");
    font-style: normal;
    unicode-range: U+AC00-D7A3;
}

* {
    font-family: 'Eulyoo1945' !important;                 
    color: inherit;
    box-sizing: border-box;
}

a:link {color:#ffffff;}
a:visited {color: #ffffff;}

// 반응형

$breakpoint-desktop: 1024px;
@mixin mobile {
    @media (max-width: #{$breakpoint-desktop - 1px}) {
      @content;
    }
}

@mixin desktop {
    @media (min-width: #{$breakpoint-desktop}) {
      @content;
    }
}